import React, { Component } from 'react'

export default class header extends Component {
    render() {
        return (
            <div className="menu">
               <nav className="navbar navbar-expand-lg navbar-light bg-light ">
  <div className="container">
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav">
        <li className="nav-item"> 
          <a className="nav-link active text-primary" aria-current="page" href="#">Home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link text-primary" href="#">Features</a>
        </li>
        <li className="nav-item">
          <a className="nav-link text-primary" href="#">Pricing</a>
        </li>
        <li className="nav-item">
          <a className="nav-link text-primary" href="#" tabindex="-1" aria-disabled="true">Disabled</a>
        </li>
      </ul>
    </div>
  </div>
</nav>
            </div>
        )
    }
}
